"use client";

import React, { useState, useEffect, useRef } from "react";
import { Search, MapPin, X } from "lucide-react";
import { CITIES, City } from "../lib/cities";

interface Props {
    onSelectCity: (city: City) => void;
}

export default function SearchBox({ onSelectCity }: Props) {
    const [query, setQuery] = useState("");
    const [isOpen, setIsOpen] = useState(false);
    const [suggestions, setSuggestions] = useState<City[]>([]);
    const [activeIndex, setActiveIndex] = useState(-1);

    const wrapperRef = useRef<HTMLDivElement>(null);

    // Filter cities Logic
    useEffect(() => {
        if (!query.trim()) {
            setSuggestions([]);
            return;
        }

        const lowerQ = query.toLowerCase();
        const matches = CITIES.filter(c => c.name.toLowerCase().includes(lowerQ));

        // Sort: Starts with query first, then others
        matches.sort((a, b) => {
            const aStarts = a.name.toLowerCase().startsWith(lowerQ);
            const bStarts = b.name.toLowerCase().startsWith(lowerQ);
            if (aStarts && !bStarts) return -1;
            if (!aStarts && bStarts) return 1;
            return 0; // maintain default order
        });

        setSuggestions(matches);
        setActiveIndex(-1); // Reset key nav
        setIsOpen(true);
    }, [query]);

    // Click Outside to Close
    useEffect(() => {
        function handleClickOutside(event: MouseEvent) {
            if (wrapperRef.current && !wrapperRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    // Keyboard Nav
    const handleKeyDown = (e: React.KeyboardEvent) => {
        if (!isOpen || suggestions.length === 0) return;

        if (e.key === "ArrowDown") {
            e.preventDefault();
            setActiveIndex(prev => (prev < suggestions.length - 1 ? prev + 1 : 0));
        } else if (e.key === "ArrowUp") {
            e.preventDefault();
            setActiveIndex(prev => (prev > 0 ? prev - 1 : suggestions.length - 1));
        } else if (e.key === "Enter") {
            e.preventDefault();
            if (activeIndex >= 0) {
                selectCity(suggestions[activeIndex]);
            }
        } else if (e.key === "Escape") {
            setIsOpen(false);
        }
    };

    const selectCity = (city: City) => {
        onSelectCity(city);
        setQuery("");
        setIsOpen(false);
    };

    // Highlighting Helper
    const getHighlightedText = (text: string, highlight: string) => {
        const parts = text.split(new RegExp(`(${highlight})`, "gi"));
        return (
            <>
                {parts.map((part, i) =>
                    part.toLowerCase() === highlight.toLowerCase() ? (
                        <span key={i} className="font-extrabold text-blue-600 bg-blue-50/50">{part}</span>
                    ) : (
                        part
                    )
                )}
            </>
        );
    };

    return (
        <div ref={wrapperRef} className="relative z-50 w-full group">
            <div className="absolute left-4 top-1/2 -translate-y-1/2 text-slate-400 group-focus-within:text-blue-500 transition-colors pointer-events-none">
                <Search size={20} />
            </div>

            <input
                type="text"
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                onKeyDown={handleKeyDown}
                onFocus={() => { if (query) setIsOpen(true) }}
                placeholder="Search city..."
                className="w-full bg-slate-100/80 border border-slate-200 text-slate-700 rounded-2xl py-3.5 pl-12 pr-10 focus:outline-none focus:ring-2 focus:ring-blue-100 focus:border-blue-500 transition-all font-medium placeholder:text-slate-400"
            />

            {query && (
                <button
                    onClick={() => { setQuery(""); setIsOpen(false); }}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-slate-600 p-1"
                >
                    <X size={16} />
                </button>
            )}

            {/* Dropdown */}
            {isOpen && query && (
                <div className="absolute top-full left-0 right-0 mt-2 bg-white rounded-2xl border border-slate-100 shadow-xl max-h-[300px] overflow-y-auto no-scrollbar py-2">
                    {suggestions.length > 0 ? (
                        <ul role="listbox">
                            {suggestions.map((city, idx) => (
                                <li
                                    key={`${city.name}-${city.country}`}
                                    role="option"
                                    aria-selected={idx === activeIndex}
                                    className={`px-4 py-3 cursor-pointer flex items-center gap-3 transition-colors ${idx === activeIndex ? "bg-blue-50 text-blue-800" : "hover:bg-slate-50 text-slate-700"}`}
                                    onClick={() => selectCity(city)}
                                    onMouseEnter={() => setActiveIndex(idx)}
                                >
                                    <MapPin size={16} className={`shrink-0 ${idx === activeIndex ? "text-blue-500" : "text-slate-400"}`} />
                                    <div className="flex flex-col">
                                        <span className="font-medium text-sm">
                                            {getHighlightedText(city.name, query)}
                                        </span>
                                        <span className="text-xs text-slate-400 opacity-80">{city.country}</span>
                                    </div>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <div className="px-4 py-6 text-center text-slate-400 text-sm">
                            No results found for "{query}"
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}
